:root {
    --color-background: #F5F2EC; /* Bege puxado para o "creme" com toque de calor  */
    --color-text: #3E3A39;       /* Grafite "Café" (Escuro e quente) */
    --color-text-title: #EAE6E3;
    --color-primary: #D95F43;    /* Laranja "Terracota" (Para botões e CTAs) */
    --color-accent: #5A6349;     /* Verde "Oliva" (Para detalhes ou subtítulos) */
    --font-family-headings: 'Merriweather', serif;
    --font-family-body: 'Inter', sans-serif;
    --color-button-background: #2D3748; /* Fundo escuro para botões secundários */
    --color-button-text: #FAF8F5; /* Texto claro para botões secundários */
    --color-text-compromise: #D95F43; /* Mesma cor primária, porém sem alterar com a mudança de light e dark mode*/
    --color-card-background: #FAF8F5; /* Cor de fundo para os cards no modo claro */
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    background-color: var(--color-background);
}

body {
    font-family: var(--font-family-body);
    transition: background-color 0.3s ease, color 0.3s ease;
    color: var(--color-text);
}

h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-family-headings);
    color: var(--color-accent);
}

a {
    color: inherit;
    text-decoration: none;
}

.title_full{
    font-family: var(--font-family-body);
    font-weight: 500;
    font-size: 24px;
}

.title_header{
    font-family: var(--font-family-body);
    font-weight: 800;
    color: var(--color-primary);

}

.nav-link {
    margin-right: 25px;
    transition: color 0.2s ease-in-out;
    position: relative;
    padding-bottom: 5px; /* Espaço para a borda inferior */
    font-size: 16px;
}

/* Efeito de hover (placeholder) para links que NÃO estão ativos */
.nav-link:hover {
    color: var(--color-primary);
}

/* Estilo para o link da página ATUAL */
.nav-link.active {
    color: var(--color-primary);
    font-weight: 600; /* Deixa o texto um pouco mais forte */
}

/* Estilo para o botão "Call to Action" (Solicitar Projeto) */
.cta-link {
    background-color: var(--color-primary);
    color: var(--color-text-title);
    padding: 10px 20px;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    display: inline-block; /* Permite que a transformação (scale) funcione */
    transition: transform 0.2s ease-in-out;
}

.cta-link:hover {
    transform: scale(1.05); /* Aumenta o tamanho do botão em 5% */
}

.theme-toggle-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 24px;
    color: var(--color-text);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.theme-toggle-button:hover {
    color: var(--color-primary);
    transform: scale(1.1);
}

.destaque_title{
    color: #D69E2E; /* Cor fixa para o destaque do texto*/
}

.main-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px; /* Adiciona mais espaçamento nas laterais */
    border-bottom: 1px solid #ddd;
    font-size: 18px;

    /* Adições para o header fixo */
    position: sticky;
    top: 0;
    z-index: 1000;

    /* Efeito de vidro fosco (frosted glass) */
    transition: background-color 0.3s ease;
    background-color: color-mix(in srgb, var(--color-background) 85%, transparent);
    -webkit-backdrop-filter: blur(8px); /* Para Safari */
    backdrop-filter: blur(8px); /* Desfoque para o conteúdo atrás */

    border-bottom: 1px solid color-mix(in srgb, var(--color-text) 15%, transparent);
}

.hero-section {
    /* Define a altura para ocupar 100% da tela menos a altura do header */
    height: calc(100vh - 70px); 
    /* Configurações da imagem de fundo */
    /* Camada 1: Overlay escuro. Camada 2: Sua imagem. */
    background-image: 
        linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url('/main-bg.jpg');
    background-size: cover; /* Garante que a imagem cubra toda a área */
    background-position: center; /* Centraliza a imagem */
    background-repeat: no-repeat; /* Evita que a imagem se repita */

    /* Centraliza o conteúdo dentro da seção */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; /* Necessário para posicionar o ícone de rolagem */
    text-align: center;
}

.hero-content h1 {
    font-size: 4rem; /* Tamanho maior para o título principal */
    color: var(--color-text-title); /* Cor fixa para o título principal */
    /* Animação de fade-in */
    opacity: 0;
    animation: fadeInUp 1s ease-out 0.5s forwards;
}

.hero-content p {
    font-size: 1.25rem;
    color: #EAE6E3; /* Cor fixa para o subtítulo */
    /* Animação de fade-in com delay */
    opacity: 0;
    animation: fadeInUp 1s ease-out 0.8s forwards;
}

/* Estilos para o indicador de rolagem */
.scroll-down-indicator {
    position: absolute;
    bottom: 30px;
    left: 50%;
    cursor: pointer;
    /* Animação de fade-in com delay maior */
    opacity: 0;
    animation: fadeInUp 1s ease-out 1.2s forwards;
}

/* Estilos para o corpo do mouse customizado */
.mouse-icon {
    width: 28px;
    height: 48px;
    border: 2px solid var(--color-text-title);
    border-radius: 15px;
    position: relative;
    animation: mouseBob 2s ease-in-out infinite; /* Movimento mais rápido */
}

/* Estilos para a rodinha de rolagem */
.mouse-scroll-wheel {
    width: 4px;
    height: 8px;
    background-color: var(--color-text-title);
    border-radius: 2px;
    position: absolute;
    top: 8px;
    left: 50%;
    transform: translateX(-50%);
    animation: scrollWheel 2s ease-in-out infinite; /* Movimento mais rápido */
}

/* Animação sutil de subida e descida para o corpo do mouse */
@keyframes mouseBob {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(5px); }
}

/* Animação para a rodinha de rolagem */
@keyframes scrollWheel {
    0% { top: 8px; opacity: 1; }
    50% { top: 14px; opacity: 1; }   /* Movimento menor */
    100% { top: 14px; opacity: 0; }  /* Movimento menor */
}

/* Animação de fade-in e subida para os elementos do hero */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Container para os botões do Hero */
.hero-buttons {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    /* Animação de fade-in com delay maior */
    opacity: 0;
    animation: fadeInUp 1s ease-out 1s forwards;
}

/* Estilo do botão primário (preenchido) */
.hero-cta-primary {
    background-color: #D69E2E; /* Cor mostarda do destaque */
    color: #2D2B29; /* Cor de texto escura para contraste */
    padding: 12px 28px;
    border-radius: 8px;
    font-weight: 600;
    min-width: 280px; /* Define uma largura mínima */
    text-align: center; /* Garante que o texto fique centralizado */
    transition: transform 0.2s ease, filter 0.2s ease;
}

.hero-cta-primary:hover {
    transform: scale(1.05);
    filter: brightness(1.1);
}

/* Estilo do botão secundário (vazado) */
.hero-cta-secondary {
    background-color: var(--color-button-background); /* Cor de fundo escura */
    color: var(--color-button-text); /* Cor de texto clara para contraste */
    padding: 12px 28px;
    border-radius: 8px;
    font-weight: 600;
    min-width: 280px; /* Define a mesma largura mínima */
    text-align: center; /* Garante que o texto fique centralizado */
    transition: transform 0.2s ease, filter 0.2s ease;
}

.hero-cta-secondary:hover {
    transform: scale(1.05);
    filter: brightness(1.2); /* Aumenta o brilho no hover */
}

.next-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 100px 40px;
    min-height: 500px;
    gap: 20px;
    border-radius: 12px; /* Adiciona bordas arredondadas para o efeito de sombra ficar mais bonito */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.next-section:hover {
    /* Escurece levemente a cor de fundo atual */
    background-color: color-mix(in srgb, var(--color-background) 98%, #000 2%);
    /* Efeito de "levantar" o card */
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.07);
}

.title_section_about{
    font-family: var(--font-family-body);
    color: var(--color-text);
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-weight: 700;
}

.subtitle_section_about{
    font-family: var(--font-family-body);
    color: var(--color-text);
    font-size: 1.125rem;
    line-height: 1.75rem;
    max-width: 700px;
}

.benefits_container{
    display: flex;
    justify-content: space-around;
    gap: 40px; /* Adiciona um espaço entre os blocos */
    margin-top: 50px; /* Adiciona um espaço acima dos benefícios */
    width: 100%;
    max-width: 1200px; /* Limita a largura máxima do container */
}

/* Estilo para cada bloco de benefício */
.benefit-item {
    display: flex;
    flex-direction: column;
    align-items: center;   /* Centraliza os itens horizontalmente */
    text-align: center;    /* Centraliza o texto */
    flex: 1; /* Faz com que cada item ocupe um espaço igual */
    max-width: 400px; /* Define uma largura máxima para cada bloco */
    background-color: color-mix(in srgb, var(--color-background) 95%, #000 5%);
    border-radius: 20px;
    padding: 2em 3em;
    border: 2px solid transparent;
    transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease; /* Transição suave */
}

/* Efeito de hover para o card de benefício */
.benefit-item:hover {
    transform: scale(1.05); /* Aumenta o tamanho do card */
    border-color: var(--color-primary); /* Adiciona a borda com a cor primária */
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1); /* Sombra para dar profundidade */
}

/* Estilo para o ícone */
.benefit-icon {
    font-size: 3rem; /* Tamanho do ícone */
    color: var(--color-primary); /* Cor do ícone */
    margin-bottom: 15px; /* Espaço abaixo do ícone */
    transition: transform 0.3s ease; /* Transição suave para o ícone */
}

.benefit-item:hover .benefit-icon {
    transform: translateY(-10px) scale(1.1); /* Desloca para cima e aumenta um pouco o ícone */
}

/* Estilo para o título do benefício */
.title_benefits {
    font-family: var(--font-family-headings);
    font-size: 1.5rem;
    color: var(--color-text);
    margin-bottom: 10px;
}

/* Estilo para o texto do benefício */
.text_benefits {
    font-size: 1rem;
    line-height: 1.6;
}

.compromise_container{
    /* Adiciona uma camada branca semi-transparente sobre a imagem */
    background-image: linear-gradient(rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3)), url('/section-bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 500px;
    padding: 60px 40px;
}

.title_section_compromise{
    font-family: var(--font-family-body);
    color: var(--color-text-title);
    font-size: 36px;
    font-weight: 700;
    text-align: center;
}

.title_compromise{
    color: var(--color-text-compromise);
}

.compromise_banner{
    display: flex;
    justify-content: center;
    align-items: stretch; /* Faz os cards terem a mesma altura */
    gap: 40px; /* Reduzindo um pouco o espaço para melhor harmonia */
    margin-top: 70px; /* Espaço abaixo do título da seção */
}

.compromise_card {
    flex: 1; /* Faz com que cada card ocupe um espaço igual */
    max-width: 420px; /* Largura máxima para cada card */
    text-align: left; /* Alinha o texto à esquerda */
    display: flex;
    position: relative; /* Necessário para posicionar o ícone de aspas */
    overflow: hidden; /* Garante que o ícone não vaze para fora do card */
    flex-direction: column;
    gap: 10px; /* Espaço entre os elementos dentro do card, exceto o ícone */
    border-radius: 20px;
    background-color: color-mix(in srgb, var(--color-background) 95%, #000 5%);
    padding: 45px; /* Padding padrão */
    padding-top: 60px; /* Aumenta o padding superior para o ícone */
    border: 2px solid transparent;
    transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.compromise_card:hover {
    transform: scale(1.05);
    border-color: var(--color-primary);
    box-shadow: 0 10px 25px color-mix(in srgb, var(--color-text) 15%, transparent);
}

/* Animação para o ícone de aspas no hover do card */
.compromise_card:hover .compromise-quote-icon {
    transform: translateY(-5px) scale(1.05);
}

/* Estilo para o ícone de aspas duplas */
.compromise-quote-icon {
    position: absolute;
    top: 5px; /* Posição dentro do padding superior */
    left: 36px;
    font-size: 3rem; /* Tamanho do ícone */
    color: var(--color-text-compromise);
    z-index: 0;
    transition: transform 0.3s ease; /* Mesma transição do benefit-icon */
}

.compromise_phase {
    font-size: 14px;
    font-weight: 700;
    color: var(--color-text-compromise);
    margin-top: 10px;
}

.compromise_item {
    font-size: 1.25rem; /* 20px */
    line-height: 1.6;
    color: var(--color-text);
    font-style: italic;
    padding-bottom: 15px; /* Espaço entre o texto e a linha */
    border-bottom: 1px solid color-mix(in srgb, var(--color-text) 15%, transparent); /* Linha separadora sutil */
    margin-bottom: 10px; /* Espaço entre a linha e o parágrafo abaixo */
}

.compromise_description {
    font-size: 0.9rem; /* 14.4px */
    color: var(--color-text); /* Cor do texto que se adapta ao tema */
}

/* Estilo para o botão na página de portfólio */
.portfolio-button {
    display: inline-block;
    margin-top: 15px;
    padding: 8px 16px;
    background-color: var(--color-text);
    color: var(--color-background);
    border-radius: 6px;
    font-weight: 500;
    transition: transform 0.2s ease, filter 0.2s ease;
}

.portfolio-button:hover {
    transform: scale(1.05);
    filter: brightness(1.2);
}

/* Substitui o media query por uma classe .dark */
:root.dark {
    --color-background: #2D2B29; /* Fundo "Café" (Bem escuro e quente) */
    --color-text: #EAE6E3;       /* Texto "Pergaminho" (Claro e quente) */
    --color-primary: #E07A5F;    /* Laranja "Terracota" (Mais brilhante para pop-up) */
    --color-accent: #8C9A79;     /* Verde "Oliva" (Mais claro) */
    --color-text-title: #EAE6E3; /* Mantém a mesma cor para títulos em fundo escuro */
    --color-card-background: #2A2726; /* Cor de fundo para os cards no modo escuro */
}